.multimedia {
  padding: 0 20px;
}

.multimedia__list {
  .clear-list;
  .tablet(
    {
      display: grid;
      grid-template-columns: 1fr 1fr;
    }
  );

  .desktop(
    {
      grid-template-columns: 1fr 1fr 1fr;
    }
  );
}

.multimedia__item {
  padding: 24px 0 25px 0;

  &--wide {
    .tablet(
      {
        grid-column: span 2;
      }
    );
    .desktop(
      {
        grid-column: span 3;
      }
    );
  }
}

.multimedia__description {
  padding-right: 0;
  padding-left: 0;
}
